@import url("https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&display=swap");
html {
  font-size: 0.875rem;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: #f2eae3;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.main {
  display: grid;
  justify-content: center;
  align-items: center;
  padding-block: 1.75rem;
  padding-inline: 1.125rem;
  margin: 0 auto;
  max-width: 695px;
  height: 100vh;
}
@media (min-width: 50em) {
  .main {
    padding: 0;
  }
}

@media (max-width: 39.9375em) {
  .footer p {
    display: none;
  }
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.c-card {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  box-shadow: 0 8px 30px rgba(49, 51, 52, 0.356);
  border-radius: 10px;
}
@media (min-width: 50em) {
  .c-card {
    flex-flow: row nowrap;
  }
}
.c-card__hero {
  flex: 1 1 50%;
}
@media (min-width: 50em) {
  .c-card__hero {
    border-radius: 10px 0 0 10px;
  }
}
.c-card__hero .c-card__img-m {
  display: block;
  border-radius: 10px 10px 0 0;
}
@media (min-width: 50em) {
  .c-card__hero .c-card__img-m {
    display: none;
  }
}
.c-card__hero .c-card__img-d {
  display: none;
  border-radius: 10px 0 0 10px;
}
@media (min-width: 50em) {
  .c-card__hero .c-card__img-d {
    display: block;
    object-fit: cover;
  }
}
.c-card__body {
  flex: 1 1 50%;
  background-color: white;
  border-radius: 0 0 10px 10px;
  padding: 1.625rem;
}
@media (min-width: 50em) {
  .c-card__body {
    border-radius: 0 10px 10px 0;
    padding-block: 3.125rem;
    padding-inline: 2.73125rem;
  }
}
.c-card__body .c-card__tag {
  font-weight: 500;
  color: #6c7289;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 0.9375rem;
}
.c-card__body .c-card__heading {
  font-family: "Fraunces", serif;
  font-weight: 700;
  font-size: 2.125rem;
  margin-bottom: 0.9375rem;
}
@media (min-width: 50em) {
  .c-card__body .c-card__heading {
    font-size: 2.5625rem;
    margin-bottom: 1.5625rem;
  }
}
.c-card__body .c-card__desc {
  font-size: 0.9375rem;
  color: #6c7289;
  margin-bottom: 3.5rem;
}
@media (min-width: 50em) {
  .c-card__body .c-card__desc {
    font-size: 1.125rem;
    margin-bottom: 4.375rem;
  }
}
.c-card__price {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin-bottom: 1.25rem;
}
.c-card__price .c-card__amt {
  font-family: "Fraunces", serif;
  font-weight: 700;
  font-size: 2.125rem;
  color: #3c8067;
}
.c-card__price .c-card__orig-amt {
  text-decoration: line-through;
  color: #6c7289;
  margin-left: 1.75rem;
}
.c-card__cta {
  display: flex;
  justify-content: center;
  border: 0 solid none;
  padding-block: 1.125rem;
  padding-inline: 5.625rem;
  margin: auto 0;
  width: 100%;
  background-image: url("/app/asset/images/icon-cart.svg");
  background-repeat: no-repeat;
  background-position: 30% 50%;
  background-color: #3c8067;
  box-shadow: 0 8px 30px rgba(101, 104, 107, 0.447);
  border-radius: 10px;
  color: white;
  transition: all 300ms ease-in-out;
}
.c-card__cta:hover {
  cursor: pointer;
  background-color: #1b4132;
}
.c-card__cta > p {
  display: inline-block;
  font-weight: 700;
  margin-left: 1.5625rem;
}

.footer__attribution {
  font-size: 1.25rem;
  text-align: center;
}
.footer__attribution a {
  color: #3e52a3;
}